<template>
	<div id="comments">
		<header :class='{"on":headerBg}'>
			<i class='iconfont icon-back' @click='$router.go(-1)'></i>
			<span>商品评论</span> 
		</header>
		<div class="label">
			<span v-for='(val,i) in grade' :class='{"on":currentGrade===i}' @click='selectGrade(i)'>
				{{val.name}} ({{val.id | filterCount}})
			</span>
		</div>
		<div class="box" v-for='(val,i) in commentData' :key='i'>
			<div class="left">
				<img :src="val.face" alt="" @click='lookImg(val.face,"avator")'>
			</div>
			<div class="right">
				<div class="name">
					{{val.username}}
					<span>{{val.date}}</span>
				</div>
				<div class="spec">
					{{val.spec}}
				</div>
				<div class="rate">
					<van-rate v-model="val.rate" readonly />
				</div>
				<div class="content">
					{{val.content}}
				</div>
				<div class="imgBox" v-show='val.img.length>0'>
					<img v-for='(item,index) in val.img' :src="item" @click='lookImg(val.img,index)'>
				</div>
			</div>
		</div>
	<van-divider :style="{borderColor:'#aaa',marginTop:'.46rem'}" v-show='commentData.length>6'>到底啦</van-divider>
	<van-image-preview
	  v-model="showBigImg"
	  :images="images"
	 :start-position='imgIndex'
	>
	</van-image-preview>
	</div>
</template>
<script>
	import commentData from '@/assets/json/comments.json'
	export default{
		name:'comments',
		data(){
			return{
				nowComments:[],
				commentData:commentData.data,
				headerBg:false,
				showBigImg: false,
			    imgIndex: 0,
			    images: [],
				grade:[
					{
						id:0,
						name:'全部'
					},
					{
						id:1,
						name:'好评'
					},
					{
						id:2,
						name:'差评'
					},
					{
						id:3,
						name:'有图'
					},
					{
						id:4,
						name:'追加'
					},

				],
				currentGrade:0
			}
		},
		methods:{
			selectGrade(grade){
				this.currentGrade=grade;
				let data=commentData.data;
				if(grade===0){
					this.commentData=data;
					return;
				}
				else if(grade===3){
					data=commentData.data.filter((val)=>{
						return val.img.length>0;
					});
				}
				else{
					data=commentData.data.filter((val)=>{
						return val.grade===grade;
					});
				}
				this.commentData=data;
			},
			lookImg(imgList,imgIndex){
				if(imgIndex==='avator'){
					this.imgIndex=0;
					this.images=[imgList];
				}
				else{
					this.images=imgList;
					this.imgIndex=imgIndex;
				}				
				this.showBigImg=true;
			},
			onChange(index) {
		      this.index = index+1;
		    }
		},
		filters:{
			filterCount(id){
				let num;
				if(id===0){
					num=commentData.data.filter((val)=>{
						return val.grade!==id;
					});
				}
				else if(id===3){
					num=commentData.data.filter((val)=>{
						return val.img.length>0;
					});
				}
				else{
					num=commentData.data.filter((val)=>{
						return val.grade===id;
					});				
				}
				return num.length;
			}
			
		},
		activated(){
			document.documentElement.scrollTop =document.body.scrollTop=0;
			this.selectGrade(0);
			window.onscroll=()=>{
				let srcoll=document.documentElement.scrollTop || document.body.scrollTop;
				srcoll>130?this.headerBg=true:this.headerBg=false;

			}
		},
		deactivated(){
			this.headerBg=false;
			this.currentGrade=0;
			window.onscroll=null;
		},
		beforeRouteLeave(to,from,next){
			if(this.showBigImg){
				this.showBigImg=false;
				next(false);
			}
			else{
				next();
			}
		}
	}
</script>
<style lang='stylus' scoped>
	#comments
		margin-bottom:1rem;
	header
		position:fixed;
		top:0;
		width:92%;
		padding:0 4%;
		height:1rem;
		font-size:.4rem;
		display:flex;
		align-items:center;
		background:#fff;
		z-index:2;
		transition:.2s;
		.iconfont
			font-size:.48rem;
		span
			position:absolute;
			left:50vw;
			transform:translateX(-50%);
			font-weight:700;
		&.on
			background:gainsboro;
	.label
		width: 92%;
		padding:0 4%;
		display:flex;
		flex-wrap:wrap;
		padding-bottom:.3rem;
		border-bottom:1px solid #ededed;
		margin-top:1rem;
		span
			padding: 0.1rem 0.1rem;
			font-size: 0.28rem;
			border: solid 1px #ddd;
			border-radius: 20px;
			width: 25%;
			box-sizing: border-box;
			margin: .1rem .2rem;
			display: inline-block;
			background: #f9f9f9;
			text-align: center;
			color: #555;
			&.on
				border: solid 1px #f06c7a;
				color: #f06c7a;
	.box
		display:flex;
		width:92%;
		margin:.3rem 4%;
		border-bottom:1px solid #eee;
		.left
			margin-right:.2rem;
			display:flex;
			img
				width: .9rem;
				height:.9rem;
				border-radius:50%;
		.right
			margin-bottom:.28rem;
			flex:2;
			.name
				font-size:.32rem;
				color:#555;
				position:relative;
				span
					position:absolute;
					right:.06rem;
					font-size:.28rem;
					color:#aaa;
					top:50%;
					transform:translateY(-50%);
			.spec
				color:#aaa;
				font-size:.26rem;
				margin:.1rem 0;
			.rate
				display:flex;
				margin:.1rem 0;
				>>>.van-rate__icon
					font-size:.24rem;
			.content
				font-size:.29rem;
				text-indent:.18rem;
			.imgBox
				margin-top:.2rem;
				img
					width: calc((84.6vw - 25px)/4);
					height: calc((84.6vw - 25px)/4);
					border-radius:5px;
					margin-right:.2rem;
</style>


